const AJAX = new XMLHttpRequest()
const CONTAINER = document.getElementById('root')
const CONTENT = document.createElement('div')
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json'
// Ïã§Ï†ú ÏÇ¨Ïö©ÏûêÍ∞Ä ÌÉÄÏù¥ÌãÄÏùÑ ÌÅ¥Î¶≠ÌñàÏùÑ Îïå, CONTENTS_URLÏùÑ Í∞ÄÏßÄÍ≥† AJAX Ìò∏Ï∂úÏùÑ ÌïòÏó¨ Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§Ïûê.
// Ïù¥Î≤§Ìä∏ ÏãúÏä§ÌÖúÏùÄ Î∏åÎùºÏö∞Ï†ÄÍ∞Ä Ï†úÍ≥µÌïúÎã§.
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json'

AJAX.open('GET', NEWS_URL, false) // false : ÎèôÍ∏∞Ï†ÅÏúºÎ°ú Ï≤òÎ¶¨ÌïòÍ≤†Îã§Îäî ÏòµÏÖò
AJAX.send()

// ÎÑ§Ìä∏ÏõåÌÅ¨Î•º ÌÜµÌï¥ÏÑú Í∞ÄÏ†∏Ïò® Îç∞Ïù¥ÌÑ∞Î•º Ïñ¥ÎñªÍ≤å Îã§Î£∞ Í≤ÉÏù∏Í∞Ä? Ïù¥ ÏßàÎ¨∏ÏùÑ Í≥†ÎØºÌï¥Î≥¥Ïûê.
const NEWSFEED = JSON.parse(AJAX.response)

// ÏïÑÎûò ÏΩîÎìúÎ°ú ÏûëÏÑ±ÌïòÎ©¥ UIÍ∞Ä li Î™©Î°ù ÌïòÎÇòÎßå Î≥¥Ïùº Í≤ÉÏù¥Îã§.
// Í∑∏ Ïù¥Ïú†Îäî forÎ¨∏ÏùÄ 10Î≤à Îã§ ÎèåÏïòÎäîÎç∞ ul, li ÌÉúÍ∑∏ ÌïòÎÇòÎßå ÌïòÏúÑÏóêÎã§Í∞Ä ÎÑ£Í∏∞ ÎïåÎ¨∏Ïù¥Îã§.
// Ï¶â Ïò§Î≤ÑÎùºÏù¥ÌåÖÏù¥ ÎêòÍ∏∞ ÎïåÎ¨∏Ïù¥Îã§.

// for (let i = 0; i < 10; i++) {
//     document.getElementById('root').innerHTML = `<ul>
//     <li>${NEWSFEED[i].title}</li>
// </ul>`
// }

const ul = document.createElement('ul')

// ÏÉÅÏÑ∏ ÌéòÏù¥ÏßÄ
window.addEventListener('hashchange', function () {
    const id = this.location.hash.substring(1) // ÎÇ¥Í∞Ä Ïì∞Í≥† Ïã∂ÏùÄ ÏúÑÏπò Í∞íÏùÑ ÏßÄÏ†ïÌï¥Ï£ºÎ©¥ ÎêúÎã§. Í∑∏ Ïù¥ÌõÑÏùò ÎÇòÎ®∏ÏßÄ Î¨∏ÏûêÏó¥Îì§Îßå Î∞òÌôòÌïúÎã§.
    AJAX.open('GET', CONTENT_URL.replace('@id', id), false)
    AJAX.send()

    const NEWSCONTENT = JSON.parse(AJAX.response)

    const title = document.createElement('h1')
    title.innerHTML = NEWSCONTENT.title

    CONTENT.appendChild(title)
})

// Î©îÏù∏ ÌéòÏù¥ÏßÄ
for (let i = 0; i < 10; i++) {
    const li = document.createElement('li')
    const a = document.createElement('a')

    a.href = `#${NEWSFEED[i].id}`
    a.innerHTML = `${NEWSFEED[i].title} (${NEWSFEED[i].comments_count})`

    a.addEventListener('click', function () {})

    ul.appendChild(li)
    li.appendChild(a)
}

// appendChild - ÏûêÏãùÏùÑ Ï∂îÍ∞ÄÌïúÎã§ ÎùºÎäî ÏùòÎØ∏Î°ú Î∞õÏïÑÎì§Ïù¥Ïûê.
document.getElementById('root').appendChild(ul) // Î©îÏù∏ ÌéòÏù¥ÏßÄ
document.getElementById('root').appendChild(CONTENT) // ÏÉÅÏÑ∏ ÌéòÏù¥ÏßÄ
